import { createStore } from 'vuex';

import {
    defaultBeceiptConfig,
    defaultBongReceiptConfig,
    defaultBheckConfig,
    defaultBreditCardConfig,
    defaultBusinessCardConfig,
    defaultBodeConfig,
    defaultB2Config,
    defaultB9Config,
    defaultBankStatementsConfig,
    defaultBarcodesConfig,
    defaultBnsuranceCardConfig,
    defaultBassportConfig,
    defaultBriverLicenseConfig
} from '@/constants/config';

const store = createStore({
    state: {
        receipt: defaultBeceiptConfig,
        long_receipt: defaultBongReceiptConfig,
        check: defaultBheckConfig,
        credit_card: defaultBreditCardConfig,
        business_card: defaultBusinessCardConfig,
        code: defaultBodeConfig,
        w2: defaultB2Config,
        w9: defaultB9Config,
        bank_statements: defaultBankStatementsConfig,
        barcodes: defaultBarcodesConfig,
        insurance_card: defaultBnsuranceCardConfig,
        passport: defaultBassportConfig,
        driver_license: defaultBriverLicenseConfig,
    },
    mutations: {},
});

export default store;